.gravatar label,
.gravatar .label {
    display: block;
    margin-bottom: 1em;
}

.gravatar .freetext {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    height: 200px;
}

.gravatar .fallback {
    border: solid 3px white;
    padding: 1px;
    margin: 2px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.gravatar .fallback:hover {
    transform: scale(1.1);
}

.gravatar .fallback.selected {
    border-color: cornflowerblue;
}

.gravatar .actions {
    text-align: center;
}

.gravatar .freetext:disabled {
    background-color: gainsboro;
}

.gravatar .actions button:disabled {
    background-color: gainsboro;
}

.gravatar .error-message {
    color: red;
}

.gravatar .visible-false {
    display: none;
}

.gravatar .visible-true {
    text-align: center;
    display: inline-block;
}
