@media print {
    header {
        display: none;
    }
}

header {
    text-align: center;
    color: white;
}

header > * {
    padding: 0.5em;
}

header a {
    padding: 0 1em;
    text-decoration: none;
    color: white;
    cursor: pointer;
}

header a:hover {
    text-decoration: underline;
}

header h1 {
    margin: 0;
    background-color: black;
    font-size: 1em;
}

header a.author {
    font-size: smaller;
    font-style: italic;
    font-weight: normal;
}

header nav {
    background-color: darkgray;
    font-size: small;
}
