.modal-overlay {
    /*
    Overlay entire viewport with black, semi-transparent background.
    */
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 98;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal-overlay.visible-false {
    /*
    Transitions to hidden, opaque, and slightly larger for visual effect.
    */
    visibility: hidden;
    opacity: 0; /* Opaque (not see-through) */
    transform: scale(1.1); /* Slightly larger */
    transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
}

.modal-overlay.visible-true {
    /*
    Transitions to visible, transparent, and original scale for visual effect.
    */
    visibility: visible;
    opacity: 1; /* Transparent (see-through) */
    transform: scale(1);
    transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
}

.modal-prompt {
    border-radius: 0.5rem;
    background-color: white;

    /*
    Horizontally and vertically center to the viewport.
    */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    /*
    Prevent height from exceeding that of the viewport.
    */
    max-height: calc(100vh - 200px); /* 100% of viewport height - 200px */
    overflow-y: auto; /* Enable vertical scrolling when necessary */
}

.modal-title {
    padding: 1em;
    background-color: cornflowerblue;
    color: white;
    font-weight: bold;

    /*
    Stick to the top when scrolling.
    */
    position: sticky;
    top: 0;

    /*
    Ensure content remains below title when scrolling
    */
    z-index: 99;

    /*
    Vertically center the contents.
    */
    display: flex;
    align-items: center;
}

.modal-title a {
    margin: 1em;
    color: white;
    font-size: smaller;
    font-weight: normal;
    font-style: italic;
    text-decoration: none;
}

.modal-title a:hover {
    text-decoration: underline;
}

.modal-title .dismiss {
    margin-left: auto; /* Align to the right */
}

.modal-title.visible-false {
    display: none;
}

.modal-content {
    padding: 2em;
}

.modal-prompt button {
    padding: 0 3em;
    font-size: 1em;
    transition: all 0.2s ease-in-out;
    background-color: cornflowerblue;
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    height: 2.5em;
}

.modal-prompt button:hover {
    cursor: pointer;
    transform: scale(1.1);
}
