.options-bar {
    justify-content: center;
    line-height: 2em;
}

.options-bar * {
    font-size: small;
}

.options-bar .layout .group {
    margin: 0 15px;
}

.options-bar label {
    padding: 0 5px;
}

.shape-option {
    display: inline-block;
    margin: 0 4px -4px 0;
    border: solid 3px gray;
    width: 12px;
    height: 12px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.shape-option:hover {
    transform: scale(1.2);
}

.shape-option.selected {
    border-color: cornflowerblue;
}

.keep-sorted {
    padding: 3px 10px;
    border: none;
    border-radius: 5px;
    background-color: gray;
    color: white;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.keep-sorted.selected {
    background-color: cornflowerblue;
}


input[type='number'] {
    width: 45px;
}

.options-bar .gravatar {
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    height: 20px;
    width: 20px;
    /* filter:grayscale(100%); */
}

.options-bar .gravatar:hover {
    transform: scale(1.2);
}

.options-bar .label:after {
    content: ':';
    margin-right: 5px;
}

.options-bar .layout .group {
    display: inline-block;
    white-space: nowrap;
}
