.role-customiser.nil-role-true {
    display: none;
}

.nil-role-true .role-badge,
.tag.passive.nil-role-true .role-badge {
    border: 1px dashed gray;
    color: lightgray;
    background-color: white !important;
    cursor: default;
}

.role-badge {
    cursor: text;
}

.nil-role-true .role-badge:before {
    /* content: 'nil'; */
    content: 'role';
}

@media print {
    .tag.nil-role-true .role-badge {
        display: none;
    }
}

.tag.nil-role-true:hover .role-badge,
.tag.nil-role-true .role-badge:focus {
    display: inline-block;
}

.tag.nil-role-true .role-badge:focus {
    color: black;
}

.tag.nil-role-true .role-badge:focus:before {
    content: '';
}



