body {
    -webkit-print-color-adjust: exact !important;
    -moz-user-select: none;
    /* fixes Firefox issue */
    font-family: arial, helvetica, sans-serif;
    font-size: 16px;
    color: dimgray;

    /* display: flex; */
    margin: 0;
    padding: 0;
}

div.app {
    width: 100%;
    height: 100%;
    /* these are needed to allow overflow to be set during modal */
}

div.app.modal-true {
    overflow: hidden; /* stops background scrolling during modal */
}


.role-list {
    transition: all ease-out 0.25s;
    position: relative;
}

.role-list.visible-false {
    display: none;
    opacity: 0;
    max-height: 0;
}

.role-list.visible-true {
    display: block;
    opacity: 1;
    max-height: 100%;
}

.role-list-container {
    position: relative;
}

.options-bar {
    /* display: none; */
    transition: all ease-out 0.25s;
}

.options-bar.visible-false {
    display: none;
    opacity: 0;
    max-height: 0;
}

.options-bar.visible-true {
    display: block;
    opacity: 1;
    max-height: 100%;
}

.options-bar-container {
    position: relative;
}

.dropzone {
    /* height: 100vh; */
    min-height: 100%;
    /* fixes inability to drag over body in Chrome and Firefox */
}

a {
    cursor: pointer;
    text-decoration: underline;
}

.image-upload-options {
    font-weight: bold;
}

.image-upload-options > * {
    margin-left: 1em;
    margin-right: 1em;
}

.image-upload-options a {
    color: cornflowerblue;
}

.image-upload-options input[type=file] {
    display: none;
}

.control-panel {
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    background-color: whitesmoke;
    position: sticky;
    top: 0;
    z-index: 97;
}



.control-panel > * {
    margin-top: 10px;
    margin-bottom: 10px;
}

div.colourPicker {
    margin-left: 5px;
    margin-right: 15px;
    
}

@media print {
    .control-panel,
    .role:empty,
    
    .tag-image {
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none !important;
        
    }

    .control-panel {
        display: none;
    }


}

.circle {
    border-radius: 50%;
    /* used in tips */
}

.square {
    border-radius: 0% !important;
    /* used in tips */
}

.label {
    display: inline-block;
    /* Needed to support first letter uppercase */
}

.label:first-letter {
    text-transform: uppercase;
}

.colour-picker {
    display: inline-block;
    /* padding-right: 15px; */
    padding-top: 3px;
    padding-bottom: 2px;
    font-size: 14px;
    width: 10px;
    cursor: pointer;
    margin-left: 5px;
    margin-right: 2px;
    
}

.colour-picker::before {
    color: gray;
    content: '\25bc';
    /* unicode zero width space character */
}

.role-customiser {
    border: 1px solid lightgray;
    background-color: white;
    padding: 5px;
    margin: 5px;
    display: inline-block;
    white-space: nowrap;
    margin-bottom: 5px;
}

.dismiss {
    float: right;
    width: 1.5rem;
    line-height: 1.5rem;
    text-align: center;
    cursor: pointer;
    border-radius: 0.25rem;
    background-color: lightgray;
    color: black;
    font-size: 14px;
    transition: all 0.2s ease-in-out;
}

.dismiss::after {
    content: '\00d7';
}

.dismiss:hover {
    background-color: darkgray;
}

.tags {
    margin-top: 1em;
    display: grid;
    grid-auto-rows: 1fr;
}

/* .row {
    margin: 8px;
} */

.tag .layout .group {
    display: block;
    margin: 8px;
}

/* .tag */

.tag {
    text-align: center !important;
    color: black;
    font-weight: bold;
    padding: 5px 0;

    /* outline */
    border-width: 1px;
    border-style: dashed;
    border-color: silver;
}




.tag-name {
    margin-bottom: 5px;
    /* for when it wraps so it doesnt get too close to role */
}

.tag-name:empty {
    border: 1px dashed gray;
    width: 20px;
    /* display: none; */
}

.tag:hover .tag-name:empty,
.tag-name:empty:focus {
    display: inline-block;
}

.tag .role-badge {
    border: 1px solid transparent;
    margin-left: 3px;
    margin-right: 3px;
    display: inline-block;
    max-width: 100%;
    /* forces name to wrap and no exceed bounds of tag */
}

.tag-image-container,
.tag-image-container * {
    box-sizing: border-box;
}

.tag-image {
    position: absolute;
    /* to overlay them */
    border: solid 5px transparent;
    background-clip: content-box;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
}

.tag-image-container {
    position: relative;
    /* to contain absolute positioned images */
    margin: 0 auto;
}


.tag-name {
    display: inline-block;
    border: 1px solid transparent;
    padding-top: 2px;
    padding-bottom: 1px;
    margin-left: 3px;
    margin-right: 3px;
    max-width: 100%;
    /* forces name to wrap and no exceed bounds of tag */
}

.tag.passive .tag-image {
    -webkit-filter: grayscale(100%);
    /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    border-color: silver !important;
}

.tag.passive .role-badge {
    background-color: silver !important;
}

/* .role-badge */

.role-badge {
    color: white;
    border-radius: 25px;
    padding-left: 7px;
    padding-right: 7px;
    padding-top: 3px;
    padding-bottom: 2px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    /* text-shadow: #000 0px 0px 2px; */
}

.label2 {
    font-size: small;
}
