.notice {
    margin: 0 5px;
    overflow-y: hidden;
    transition: all ease-out 0.25s;
}

.notice.visible-false {
    opacity: 0;
    max-height: 0;
}

.notice.visible-true {
    opacity: 1;
    max-height: 80px;
}

.notice {
    display: inline-block;
    margin: 0 auto;
    font-size: small;
    border-radius: 0.25rem;
    background-color: white;
}

.notice-content {
    padding: 1em 4em;
}
